<template>
    <div class="card" style="height: 95%;">
        <div class="card__title">
          <b-row>
            <b-col>
                <span>Driver Advance</span>
            </b-col>
            <b-col style="max-width: max-content !important;" class="col-right">
                <span>
                  <ABSButton
                    :text="'Print'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doPrint"
                  />
                </span>
                <span>
                  <ABSButton
                    :text="'View All'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="onViewAll"
                  />
                </span>
            </b-col>
          </b-row>
        </div>
        <div class="card__body">
          <div>
              <ACCFormList
                  :prop="propList"
                  :title="''"
                  @rowClicked="rowClicked"
                  @onRenderData="onRenderData"
                  ref="ref_list"
              >
              <template slot="advance_amt" slot-scope="data">
                <div style="text-align: right;">{{isCurrency(data.item.advance_amt, decimal)}}</div>
              </template>
              </ACCFormList>
          </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/ST/ST_RefundDriverAdvance",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
    };
  },
  computed: {},
  methods: {
    rowClicked(record, index) {
      let param = record;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "ST_DriverAdvanceView" });
    },
    RenderData() {
      this.$refs.ref_list.doGetList("");
    },
    doPrint() {
      this.$router.push({ name: "ST_DriverAdvancePrint" });
    },
    onViewAll() {
      this.$router.push({ name: "ST_DriverAdvance" });
    },
    onRenderData(data) {
      if (!data) return
    }
  },
  mounted() {
    this.RenderData();
  },
};
</script>

<style scoped>
</style>